import React, { useState } from 'react';
import Particles from 'react-particles-js';
import styles from './CustomParticles.module.css';

const particlesOptions = {
  particles: {
    color: {
      value: '#ededed',
    },
    number: {
      value: 160,
      density: {
        enable: false,
      },
    },
    size: {
      value: 10,
      random: true,
    },
    move: {
      direction: 'bottom',
      out_mode: 'out',
    },
    line_linked: {
      enable: false,
    },
  },
  interactivity: {
    events: {
      onclick: {
        enable: true,
        mode: 'push',
      },
      onhover: {
        enable: true,
        mode: 'repulse',
      },
    },
    modes: {
      remove: {
        particles_nb: 10,
      },
      repulse: {
        distance: 200,
        duration: 0.4,
      },
    },
  },
};

const CustomParticles = () => {
  const [imageIndex, updateImageIndex] = useState(0);
  const images = [
    'https://isaaccolls.website/data/images/HomeBanner/home-bg-1.jpg',
    'https://isaaccolls.website/data/images/HomeBanner/home-bg-2.png',
  ];
  setInterval(() => {
    const auxIndex = imageIndex >= images.length ? 0 : imageIndex + 1;
    updateImageIndex(auxIndex);
  }, 1000);
  return (<Particles params={particlesOptions} className={styles.particles} />);
};

export default CustomParticles;
